// import $store from '../../store/index.js';

export default {
	//
	baseUrl: "http://localhost:3000",
	//Promise
	request(url, options, showLoading = false, contentType = 1) {
		//
		let requeUrl = this.baseUrl + url
		return new Promise((resolve, reject) => {
			// 
			uni.request({
				url: requeUrl,
				method: options.method,
				timeout: 20000,
				data: options.data, // JSON.stringify(options.data)
				header: {
					'Content-Type': contentType === 1 ? 'application/json;charset=UTF-8' : 'application/x-www-form-urlencoded',
					// "Authorization": token?token:'',
				},
				success: (res) => {
					if (res.statusCode === 200) {
						resolve(res.data)
					} else if (res.staticCode === 401) {
						uni.showToast({
							icon: "none",
							title: "",
							duration: 1500
						})
					} else if (res.staticCode === 405) {
						uni.showToast({
							icon: "none",
							title: "",  
							duration: 1500
						})
					} else {
						uni.showToast({
							icon: "none",
							title: "" + res.staticCode,
							duration: 1500
						})
					}
				},
				fail: (error) => {
					uni.showToast({
						title: error.errMsg || '',
						icon: 'none'
					});
					return reject(error)
				}

			})
		})
	},
	// get
	get(url, data = {}) {
		return this.request(url, {
			method: 'GET',
			data: data
		})
	},

	// post
	post(url, data = {}) {
		return this.request(url,{
			method: 'POST',
			data: data
		})
	},
	
	// put
	put(url, data = {}) {
		return this.request(url,{
			method: 'PUT',
			data: data
		})
	},
	// delete
	delete(url, data = {}) {
		return this.request(url,{
			method: 'DELETE',
			data: data
		})
	},
}
